import { fireStore } from 'fbase';
// import { v4 as uuidv4 } from 'uuid';

const addLandingMessage = async (landingMessage) => {
  try {
    let data = await fireStore.collection('landing').doc('messages').get();
    if (data.exists) {
      let messageList = Array.from(data.data().messageList);
      messageList.push(landingMessage);
      await fireStore
        .collection('landing')
        .doc('messages')
        .update({ messageList });
      return null;
    } else {
      await fireStore
        .collection('landing')
        .doc('messages')
        .set({ messageList: [landingMessage] });
      return null;
    }
  } catch (error) {
    return error;
  }
};

const getPermission = async (permissionCode) => {
  const trimCode = String(permissionCode).replace(/(\s*)/g, '').toLowerCase();
  const state =
    (await fireStore.collection('permission').doc('code').get()).data()
      .permissionCode === trimCode;
  if (state) {
    localStorage.setItem('isPermission', state);
  }
  return state;
};

export { getPermission, addLandingMessage };
